import React, { useState, createContext, useEffect, useContext } from 'react';
import { useParams } from 'react-router-dom';
import LoadingScreen from 'react-loading-screen';
import { DareRootContext } from '../../App';
import { DareClientContext } from '../../App';
import QuizDeleted from './QuizDeleted';
import FriendChallengeName from './FriendChallengeName';
import OwnerChallengeScoreCard from './OwnerChallengeScoreCard';
import FriendQuizContainer from './FriendQuizContainer';
import FriendAttempterScore from './FriendAttempterScore';
import { setLocalStorageChallenger } from '../../utilities/setLocalstorage';
import Header from '../Common/Header';
import besti from '../../Assets/besti.png';

export const DareChallengerContext = createContext();

function FriendLayout() {
    const params = useParams();
    const { siteData, handleRootClick } = useContext(DareRootContext);
    const { naviagation, categorylist } = useContext(DareClientContext);
    const Challenger = JSON.parse(localStorage.getItem("Challenger"));
    const [challengerNavigation, SetChallengerNavigation] = useState({ ...Challenger })

    const [IsQuizExist, SetIsQuizExist] = useState();
    const [isDataLoaad, SetisDataLoaad] = useState(false);
    const [CheckisQuizOwner, SetCheckisQuizOwner] = useState(false);
    const [QuizAlreadyAttempt, SetQuizAlreadyAttempt] = useState(false);
    const [isAttemptQuizCompleted, SetisAttemptQuizCompleted] = useState(false);


    const [getScored, setGetScroed] = useState([]);
    const [userName, SetUsername] = useState();
    const [quizTypeId, SetQuizTypeId] = useState();


    useEffect(() => {
        // console.log(naviagation)
        if (params?.challengeId !== undefined) {
            const fetchData = async () => {
                // if (params?.challengeId !== undefined) {
                const response = await fetch(`${process.env.REACT_APP_BASE_URL}/getAllChallengerByQuizId/${params?.challengeId}`);
                SetisDataLoaad(true)
                if (!response.ok) {
                    SetIsQuizExist(false)
                } else {
                    SetIsQuizExist(true)
                    return response.json()
                }
                // }
            };
            fetchData().then((res) => {
                // console.log(naviagation?.OwnerQuizs.some(x=>x.OwnerQuizId ===params?.challengeId))
                if (naviagation?.OwnerQuizs.some(x => x.OwnerQuizId === params?.challengeId)) {
                    SetCheckisQuizOwner(true)
                    setGetScroed(res?.quiz_performer);
                    SetUsername(res?.quiz_owner);
                    SetQuizTypeId(res?.t_id)
                    SetChallengerNavigation({ ...challengerNavigation, "quizOwnerName": res?.quiz_owner, "CheckisQuizOwner": true })
                }
                if (!naviagation?.OwnerQuizs.some(x => x.OwnerQuizId === params?.challengeId)) {
                    SetCheckisQuizOwner(false);
                    setGetScroed(res?.quiz_performer);
                    SetUsername(res?.quiz_owner);
                    SetQuizTypeId(res?.t_id)
                    SetChallengerNavigation({ ...challengerNavigation, "quizOwnerName": res?.quiz_owner, "CheckisQuizOwner": false })
                    if (challengerNavigation?.QuizResponceList && challengerNavigation?.QuizResponceList.some(x => x === params?.challengeId)) {
                        SetQuizAlreadyAttempt(true);
                    }
                    else {
                        SetQuizAlreadyAttempt(false)
                    }
                }
            }).catch(
                er => {
                }
            )
        }
    }, [naviagation]);


    const handleChallengerClick = (e) => {
        if (e.hasOwnProperty("FriendNameChallengerName")) {
            SetChallengerNavigation({ ...challengerNavigation, "FriendNameChallengerName": e.FriendNameChallengerName })
            setLocalStorageChallenger({ ...challengerNavigation, "FriendNameChallengerName": e.FriendNameChallengerName })
        }
        if (e.hasOwnProperty("isAttemptQuizCompleted")) {
            SetisAttemptQuizCompleted(e?.isAttemptQuizCompleted)
            SetChallengerNavigation({ ...challengerNavigation, "QuizResponceList": challengerNavigation?.QuizResponceList ? [...challengerNavigation?.QuizResponceList, e.AttemptQuizId] : [e.AttemptQuizId] })
            setLocalStorageChallenger({ ...challengerNavigation, "QuizResponceList": challengerNavigation?.QuizResponceList ? [...challengerNavigation?.QuizResponceList, e.AttemptQuizId] : [e.AttemptQuizId] })
            setGetScroed([...getScored, e.newQuizData]);
        }
    }


    return (<>

        {isDataLoaad &&
            <DareChallengerContext.Provider value={{ challengerNavigation, naviagation,quizTypeId, handleRootClick,categorylist, handleChallengerClick, params, siteData, getScored, userName }}>
                {!IsQuizExist && <QuizDeleted />}

                {IsQuizExist && CheckisQuizOwner && <OwnerChallengeScoreCard />}
                {IsQuizExist && !CheckisQuizOwner && QuizAlreadyAttempt && <FriendAttempterScore />}
                {IsQuizExist && !CheckisQuizOwner && !QuizAlreadyAttempt && !challengerNavigation.FriendNameChallengerName && <FriendChallengeName />}
                {IsQuizExist && !CheckisQuizOwner && !QuizAlreadyAttempt && !!challengerNavigation.FriendNameChallengerName && !isAttemptQuizCompleted && <FriendQuizContainer />}

                {IsQuizExist && !CheckisQuizOwner && !QuizAlreadyAttempt && !!challengerNavigation.FriendNameChallengerName && isAttemptQuizCompleted && <FriendAttempterScore />}
            </DareChallengerContext.Provider >
        }
        {!isDataLoaad && <h1> <LoadingScreen
            loading={true}
            bgColor='#1c1b1b'
            spinnerColor='#FFFFFF'
            textColor='#FFFFFF'
            logoSrc={besti}
            text='Please Wait! your quiz is being creating.'>
        </LoadingScreen></h1>}
    </>)
}
export default FriendLayout;