import friendshipdare from '../Assets/friendshipdare.png';
import lovedare from '../Assets/lovedare.png';
import foodchallenge from '../Assets/foodchallenge.png';
import sisterquiz from '../Assets/sisterquiz.png';
import superdare from '../Assets/superdare.png';
import top5dare from '../Assets/top5dare.png';
import truefalse from '../Assets/truefalse.png';

export const setLocalStorage = (e) => {
  if (e) {
    localStorage.setItem("OwnerQuizItems", JSON.stringify(e));
  }
}

export const setLocalStorageChallenger = (e) => {
  if (e) {
    localStorage.setItem("Challenger", JSON.stringify(e));
  }
}

export const IsOwnNewChallenge = (e) => {
  if (localStorage.getItem("OwnerQuizItems") !== null) {
    try {
      const localStorageOwnerQuiz = JSON.parse(localStorage.getItem("OwnerQuizItems"));
      if (localStorageOwnerQuiz?.OwnerQuizId === e) {
        return true;
      }
      return false;
    }
    catch (e) {
      return false;
    }
  }
  return false;
}

export const CreateOwnNewChallenge = (e) => {
  if (localStorage.getItem("OwnerQuizItems") !== null) {
    try {
      const localStorageOwnerQuiz = JSON.parse(localStorage.getItem("OwnerQuizItems"));
      // console.log(localStorageOwnerQuiz)
      if (localStorageOwnerQuiz?.OwnerQuizs.length === 0) {
        const restOwnerQuiz = {
          "OwnerQuizs": [],
          "isOwnerQuestionCompleted": false,
          "quizOwnerName": "",
          "quizType": 1
        }
        localStorage.setItem("OwnerQuizItems", JSON.stringify(restOwnerQuiz));
        return true;
      }
      return false;
    }
    catch (e) {
      return false;
    }
  }
  return false;
}

export const DeleteOwnerQuizChallenge = (e) => {
  if (localStorage.getItem("OwnerQuizItems") !== null) {
    try {
      const localStorageOwnerQuiz = JSON.parse(localStorage.getItem("OwnerQuizItems"));
      // console.log(localStorageOwnerQuiz)
      if (localStorageOwnerQuiz?.OwnerQuizs.length > 0 && localStorageOwnerQuiz.OwnerQuizs.findIndex(x => x.OwnerQuizId === e) >= 0) {
        const index = localStorageOwnerQuiz.OwnerQuizs.find(x => x.OwnerQuizId === e);
        // console.log(localStorageOwnerQuiz.OwnerQuizs.filter(x => x.OwnerQuizId !== index.OwnerQuizId))
        const restOwnerQuiz = {
          "OwnerQuizs": [...localStorageOwnerQuiz.OwnerQuizs.filter(x => x.OwnerQuizId !== index.OwnerQuizId)],
          "isOwnerQuestionCompleted": false,
          "quizOwnerName": "",
          "quizType": 1
        }
        localStorage.setItem("OwnerQuizItems", JSON.stringify(restOwnerQuiz));
        return true;
      }
      else {
        const restOwnerQuiz = {
          "OwnerQuizs": [],
          "isOwnerQuestionCompleted": false,
          "quizOwnerName": "",
          "quizType": 1
        }
        localStorage.setItem("OwnerQuizItems", JSON.stringify(restOwnerQuiz));
        return true;
      }
    }
    catch (e) {
      return false;
    }
  }
  return false;
}
export const CreateAttempterNewQuiz = () => {
  if (localStorage.getItem("OwnerQuizItems") == null) {
    try {
      // console.log(localStorage.getItem("OwnerQuizItems"))
      const restOwnerQuiz = {
        "OwnerQuizs": [],
        "isOwnerQuestionCompleted": false,
        "quizOwnerName": "",
        "quizType": 1
      }
      localStorage.setItem("OwnerQuizItems", JSON.stringify(restOwnerQuiz));
      return true;
    }
    catch (e) {
      return false;
    }
  }
  return false;
}

export const stringToBoolean = (string) => {
  return string === 'false' ? false : !!string
};


