export const GetImageByQuizID = (e) => {
  switch (e) {
    case 1:
      return friendshipdare

    case 2:
      return lovedare

    case 3:
      return foodchallenge
    case 4:
      return sisterquiz

    case 5:
      return superdare

    case 6:
      return truefalse

    case 7:
      return top5dare

    default:
      return friendshipdare
  }
};

export const loadImage = image => {
  return new Promise((resolve, reject) => {
    const loadImg = new Image()
    loadImg.src = image
    loadImg.onload = () => resolve(image)
    // loadImg.onerror = err => reject(err)
  })
}