import React, { useContext} from 'react';
import { useNavigate } from 'react-router';
import PieChart from 'react-simple-pie-chart';
import { DareRootContext } from '../../App';
import { DareChallengerContext } from './FriendLayout'
import { CreateAttempterNewQuiz } from '../../utilities/setLocalstorage';

function FriendAttempterScore() {
    const navigate = useNavigate();
    const { siteData  } = useContext(DareRootContext);
    const { challengerNavigation, userName, getScored, categorylist,quizTypeId} = useContext(DareChallengerContext);

    const createOwnQuiz = () => {
        if (CreateAttempterNewQuiz()) {
            navigate('/')
        }
        else{
            navigate('/')
        }
    }
console.log(categorylist)
console.log(quizTypeId)
    return (
        <div className="container">
            <div className="col-md-8 offset-md-2">
                <div className="card mt-3">
                    <div className="card-body p-4 shadow text-center">
                    {
                        categorylist &&quizTypeId &&
                       <img
                            className="max-width-150 img-thumbnail border-1-grey mb-2"
                            src={`${process.env.REACT_APP_BASE_URL}/public/${categorylist && categorylist[categorylist.findIndex(x => (x.t_id === quizTypeId))].t_image}`}
                            alt={`${siteData?.site_og_image}`}
                        /> }
                      
                      
                        {/* <img
                            className="max-width-150  img-thumbnail border-1-grey mb-2"
                            src={`${process.env.REACT_APP_BASE_URL}/public${siteData?.site_og_image}`}
                            // src={`${ GetImageByQuizID(naviagation.quizType)}`}
                            alt={`${siteData?.site_og_image}`}
                        /> */}
                        <h4>{categorylist && categorylist[categorylist.findIndex(x => (x.t_id === quizTypeId))].t_title} {new Date().getFullYear()}</h4>
                        <div className=" text-center">
                            <div className='pieChart-attempter'>
                                {getScored && getScored.length !== 0 && <div>
                                    <PieChart
                                        slices={[
                                            {
                                                color: '#198754',
                                                value: getScored?.filter(x => x?.c_name?.toLowerCase() === challengerNavigation?.FriendNameChallengerName?.toLowerCase())[0]?.c_score,
                                            },
                                            {
                                                color: '#f00',
                                                value: 20 - getScored?.filter(x => x?.c_name?.toLowerCase() === challengerNavigation?.FriendNameChallengerName?.toLowerCase())[0]?.c_score,
                                            },
                                        ]}
                                    />
                                    <span className='pieChartScore'>
                                        {getScored?.filter(x => x?.c_name?.toLowerCase() === challengerNavigation?.FriendNameChallengerName?.toLowerCase())[0]?.c_score}/{20}
                                    </span>
                                </div>
                                }

                            </div>

                            <h5>Who Knows {userName} Best?</h5>
                            <table className="table table-bordered table-striped">
                                <thead >
                                    <tr>
                                        <th className="bg-danger text-white">Name</th>
                                        <th className="bg-danger text-white">Score</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {getScored && getScored?.sort((a, b) => { return b?.c_score - a?.c_score })?.map((x, i) => {
                                        return (<tr key={i}>
                                            <td className={challengerNavigation?.FriendNameChallengerName?.toLowerCase() == x?.c_name ? 'text-capitalize fw-bold text-bg-primary' : 'text-capitalize'}>
                                                {x?.c_name}
                                            </td>
                                            <td className={challengerNavigation?.FriendNameChallengerName?.toLowerCase() == x?.c_name ? 'fw-bold text-bg-primary' : ''}>
                                                {x?.c_score}
                                            </td>
                                        </tr>)
                                    })}
                                </tbody>
                            </table>
                        </div>
                        <a onClick={createOwnQuiz} className="btn btn-warning">Create Your Own Quiz</a>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default FriendAttempterScore;