import React, { useContext, useEffect, useState } from 'react';
import { DareClientContext, DareRootContext } from '../../App'
import OwnerCompeletionCard from './OwnerCompeletionCard';
import { GetImageByQuizID } from '../../utilities/setLocalstorage';
import Instruction from '../Common/Instruction';
import SelectedQuizInfo from '../Common/SelectedQuizInfo';
import { Link } from 'react-router-dom';

import tpencil from '../../Assets/t-pencil.png'
function OwnerNameCard() {

    const { handleRootClick } = useContext(DareRootContext);
    const { naviagation, handleUserClick, categorylist } = useContext(DareClientContext);
    const [username, setuserName] = useState(naviagation?.quizOwnerName ? naviagation?.quizOwnerName : '');
    const [quizType, setquizType] = useState(1);

    const handleStartCardEventFn = () => {
        if (!!username) {
            handleUserClick({ "quizOwnerName": username.trim() })
        }
        else {
            handleRootClick({ "toast": "Full name is required" });
        }
    };

    useEffect(() => {
        // console.log(naviagation)
        setquizType(naviagation.quizType)
    }, [naviagation, categorylist]);

    const usernameUpdate = (e) => {
        setuserName(e.target.value.trim())
    }


    return (
        <div className="container">
            {(!naviagation?.OwnerQuizs?.some(x => x.quizType === naviagation?.quizType)) &&
                <div className="col-md-8 offset-md-2 mt-3">
                    <div className="card  mt-3 shadow">
                        <div className="card-body">
                            <div className='text-center'>
                                {categorylist && categorylist.length > 0 && <img
                                    className="max-width-150 img-thumbnail"
                                    src={`${categorylist?.findIndex(x => x.t_id == naviagation.quizType) > -1 ? process.env.REACT_APP_BASE_URL + '/public/' + categorylist[categorylist?.findIndex(x => x.t_id == naviagation.quizType)].t_image : process.env.REACT_APP_BASE_URL + '/public/' + categorylist[0]?.t_image}`}
                                    alt={`${naviagation?.quizType}`}
                                />}
                                {/* <img
                                className="max-width-150 img-thumbnail"
                                src={`${process.env.REACT_APP_BASE_URL}/public${siteData?.site_og_image}`}
                                alt={`${siteData?.site_og_image}`}
                            /> */}
                                <h4 className='my-3'>Do Your Friends Know You?</h4>
                            </div>
                            <h5>Tips!</h5>
                            <ul className="list-unstyled text-start">
                                <li><img src={tpencil} className="img-15 me-2" />Enter your name</li>
                                <li><img src={tpencil} className="img-15 me-2" />Answer any 20 Questions</li>
                                <li className='d-flex'><img src={tpencil} className="img-15 me-2" /><p className='mb-0'> Share the quiz-link with your friends and family</p></li>
                                <li><img src={tpencil} className="img-15 me-2" />Check their scores at the scoreboard</li>
                            </ul>


                            <div className="input-group mb-3">
                                <span className="input-group-text"><svg style={{ width: '20px' }} viewBox="0 0 32 32" xmlns="http://www.w3.org/2000/svg"><defs></defs><title /><g data-name="Layer 7" id="Layer_7"><path className="cls-1" d="M19.75,15.67a6,6,0,1,0-7.51,0A11,11,0,0,0,5,26v1H27V26A11,11,0,0,0,19.75,15.67ZM12,11a4,4,0,1,1,4,4A4,4,0,0,1,12,11ZM7.06,25a9,9,0,0,1,17.89,0Z" /></g></svg></span>
                                <input maxLength={20} value={username} type="text" onChange={usernameUpdate} className="form-control btn-box-shadow-none" placeholder="Username" />
                            </div>
                            <div className='text-center'>
                                <button className="bg-danger btn text-white w-50" onClick={handleStartCardEventFn}>👉 Start 👈</button>

                            </div>
                            <div className='text-center mt-3'>
                                <Link className="bg-danger btn text-white w-50" to={'/'}>Back to Home Page</Link >
                            </div>
                            <div className='mt-4'>
                                <span className='text-danger'><strong>Note<sup>*</sup></strong> After 7 days your link will not work, it expired automatically.</span>
                            </div>
                        </div>
                        <Instruction />
                    </div>
                    <SelectedQuizInfo quizType={quizType} />
                </div>
            }
            {(naviagation?.OwnerQuizs?.some(x => x.quizType === naviagation?.quizType)) && <OwnerCompeletionCard />}
        </div>
    );
}

export default OwnerNameCard;