import React, { useState, useContext, useEffect } from "react";
import { DareAdminContext } from "../AdminLayout";
// import { ProfanityEngine } from '@coffeeandfun/google-profanity-words';

function QuestionForm() {
    const { token, handleAdminClick } = useContext(DareAdminContext);
    // const FetchToken = JSON.parse(token);
    const [question, setQuestion] = useState("");
    const [categoryId, setcategoryId] = useState();
    const [options, setOptions] = useState([{ a_text: "", a_image: null }]);

    // const profanity = new ProfanityEngine({ language: 'en' });
    // const allWords = profanity.all();

    const handleQuestionChange = (e) => {
        setQuestion(e.target.value);
    };
    const handleOptionChange = (e, optionIndex) => {
        // const hasCurseWords = profanity.hasCurseWords(e.target.value);
        // console.log(hasCurseWords)
        const updatedOptions = [...options];
        updatedOptions[optionIndex].a_text = e.target.value;
        setOptions(updatedOptions);
    };

    const handleImageChange = (e, optionIndex) => {
        const file = e.target.files[0];
        if (file) {
            const updatedOptions = [...options];
            updatedOptions[optionIndex].a_image = file;
            setOptions(updatedOptions);
        }
    };

    const handleAddOption = () => {
        const updatedOptions = [...options, { a_text: "", a_image: null }];
        setOptions(updatedOptions);
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        // console.log(e)
        if (!!categoryId && (!options.some(e => e.a_image == null || e.a_text == '')) && !!question) {
            const formData = new FormData();
            formData.append("q_text", question);
            formData.append("t_id", categoryId);
            options.forEach((option, index) => {
                formData.append(`options[${index}][a_text]`, option.a_text);
                formData.append(`options[${index}][a_image]`, option.a_image);
            });

            try {
                await fetch(
                    `${process.env.REACT_APP_BASE_URL}/createQuestionWithAnswers`,
                    {
                        method: "POST",
                        headers: { Authorization: token.accessToken },
                        body: formData,
                    }
                ).then((e) => {
                    handleAdminClick({ "newQuestionCreated": true })
                });
            }
            catch (error) { console.error("Error:", error); }

        }
        else {
            handleAdminClick({ toast: "Enter the required fields first" })
        }


    };
    const handleRemoveOption = (index) => {
        const option = [...options];
        option.splice(index, 1);
        setOptions(option);
    };


    const [categorylist, setCategoryList] = useState();

    const fetchDataList = () => {
        const fetchData = async () => {
            const response = await fetch(`${process.env.REACT_APP_BASE_URL}/getAllTypes`,
                { method: "GET", headers: { Authorization: token.accessToken } });
            if (!response.ok) {
                throw new Error('Data coud not be fetched!')
            } else {
                return response.json()
            }
        }

        fetchData().then((res) => {
            setCategoryList(res);
            // console.log(res)
        }).catch(
            er => {
                console.log(er)
            }
        )
    }

    useEffect(() => {
        fetchDataList()
    }, [])
    return (
        <div className="container mt-2">
            <div className="d-flex justify-content-between">
                <h2>Add New Question</h2>
                <button
                    type="button"
                    className="btn btn-success"
                    onClick={handleSubmit}
                >
                    Submit {options.length < 2 ? "Answer" : "Answers"}
                </button>
            </div>
            <hr />
            <div className="mb-3">
                <label htmlFor="Question" className="form-label">
                    Question Category <sup>*</sup>
                </label>

                <select required={true} className="form-control" onChange={(e) => { setcategoryId(e.target.value) }}>
                    <option disabled selected>Select Question Category</option>
                    {categorylist?.length > 0 && categorylist?.map((e, i) => (
                        <option key={i} value={e?.t_id}>
                            {e?.t_title} <img src={`${process.env.REACT_APP_BASE_URL}/public/${e?.t_image}`} alt="Besti" className="max-width-150" />
                        </option>
                    ))}
                </select>
            </div>


            <div className="mb-3">
                <label htmlFor="Question" className="form-label">
                    Enter Question<sup>*</sup>
                </label>
                <input
                    type="text"
                    className="form-control"
                    id="Question"
                    value={question}
                    required={true}
                    onChange={handleQuestionChange}
                    placeholder="i.e 'If you win a lottery what will you buy'"
                />
            </div>


            <div>
                {options &&
                    options.map((option, optionIndex) => (
                        <div key={optionIndex}>
                            <div className="d-flex justify-content-between">
                                <h5> Answer #{optionIndex + 1}</h5>
                                <div className="btn-box">
                                    {optionIndex >= 1 && (
                                        <button
                                            className="btn btn-danger"
                                            onClick={() => handleRemoveOption(optionIndex)}
                                        >
                                            Remove
                                        </button>
                                    )}
                                </div>
                            </div>

                            <div className="d-flex justify-content-between" key={optionIndex}>
                                <div className="container">
                                    <label htmlFor="Answer">Enter Answer<sup>*</sup></label>
                                    <input
                                        className="form-control"
                                        //   name="Answer"
                                        placeholder="Enter Answer"
                                        value={option.a_text}
                                        required={true}
                                        onChange={(e) => handleOptionChange(e, optionIndex)}
                                    />
                                    <div className="form-group mt-3">
                                        <label htmlFor="img">Select Image For answer<sup>*</sup></label>
                                        <input
                                            type="file"
                                            className="form-control"
                                            name="img"
                                            accept="image/*"
                                            onChange={(e) => handleImageChange(e, optionIndex)}
                                            placeholder="select image"
                                            required={true}
                                        />
                                        <span className="text-danger">

                                            Image must be 200X200 pixel in size<sup>*</sup>
                                        </span>
                                    </div>
                                </div>

                            </div>
                            <div className="mt-3">
                                {options.length - 1 === optionIndex && (
                                    <button className="btn btn-success" onClick={handleAddOption}>
                                        Add
                                    </button>
                                )}
                            </div>
                        </div>
                    ))}
            </div>
        </div>

    );
}

export default QuestionForm;
