import React, { useContext, useRef } from 'react';
import {DareChallengerContext} from './FriendLayout'
import { CreateAttempterNewQuiz } from '../../utilities/setLocalstorage';

import { useNavigate } from 'react-router';
function FriendChallengeName() {
    const navigate = useNavigate();

    const inputhandleUserNameRef = useRef(null);
    const { challengerNavigation,categorylist,quizTypeId, siteData, getScored, handleChallengerClick, handleRootClick } = useContext(DareChallengerContext);

    const handleStartCardEventFn = () => {
        if (!!inputhandleUserNameRef.current.value) {
            handleChallengerClick({ "FriendNameChallengerName": inputhandleUserNameRef.current.value })
        }
        else {
            handleRootClick({ "toast": "Full name is required" })
        }
    };

    const createOwnQuiz = () => {
        if (CreateAttempterNewQuiz()) {
            navigate('/')
        }
        else {
            navigate('/')
        }
    }
    return (
        <div className="container">
            <div className=" col-md-6  offset-md-3">
                <div className="card  mt-3">
                    <div className="card-header card-body shadow text-center">
                       {
                        categorylist &&quizTypeId &&
                       <img
                            className="max-width-150 img-thumbnail"
                            src={`${process.env.REACT_APP_BASE_URL}/public/${categorylist && categorylist[categorylist.findIndex(x => (x.t_id === quizTypeId))].t_image}`}
                            alt={`${siteData?.site_og_image}`}
                        /> }
                       <h4>{categorylist &&quizTypeId && categorylist[categorylist.findIndex(x => (x.t_id === quizTypeId))].t_title} {new Date().getFullYear()}</h4>
                        <p>How Well You Really Know <strong>{challengerNavigation?.quizOwnerName} </strong></p>
                        <div className="input-group mb-3">
                            <span className="input-group-text"><svg style={{ width: '20px' }} viewBox="0 0 32 32" xmlns="http://www.w3.org/2000/svg"><defs></defs><title /><g data-name="Layer 7" id="Layer_7"><path className="cls-1" d="M19.75,15.67a6,6,0,1,0-7.51,0A11,11,0,0,0,5,26v1H27V26A11,11,0,0,0,19.75,15.67ZM12,11a4,4,0,1,1,4,4A4,4,0,0,1,12,11ZM7.06,25a9,9,0,0,1,17.89,0Z" /></g></svg></span>
                            <input minLength={3} maxLength={20} ref={inputhandleUserNameRef} type="text" className="form-control btn-box-shadow-none" placeholder="Enter your full name" />
                        </div>
                        <div>
                            <button className="bg-danger btn text-white border-radius-100 p-2" onClick={handleStartCardEventFn}>👉Start👈</button>
                        </div>
                        <div className='my-3'>
                            <h5>Who Knows  <strong>{challengerNavigation?.quizOwnerName} </strong> Best?</h5>
                            <div className="text-center">
                                <table className="table table-bordered table-striped">
                                    <thead >
                                        <tr>
                                            <th className="bg-danger text-white">Name</th>
                                            <th className="bg-danger text-white">Score</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {getScored && getScored?.map((x, i) => {
                                            return (<tr key={i}>
                                                <td className='text-capitalize'>
                                                    {x.c_name}
                                                </td>
                                                <td>
                                                    {x.c_score}
                                                </td>
                                            </tr>)
                                        })}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                        <a onClick={createOwnQuiz} className="btn btn-warning mt-2">Create Your Own Quiz</a>
                    </div>
                </div>
            </div>
        </div>

    );
}
export default FriendChallengeName;