


import { useState, useEffect, useContext } from "react";
import { useParams } from 'react-router-dom';
import { DareChallengerContext } from './FriendLayout';
import {loadImage} from '../../utilities/setLocalstorage';
import wrongSelect from '../../Assets/beep-01.mp3';
import CorrectSelect from '../../Assets/beep-02.mp3';


function FriendQuizContainer() {
    const { challengerNavigation, handleChallengerClick } = useContext(DareChallengerContext);
    const [currentQuestion, setCurrentQuestion] = useState([]);
    const [currentQuestionIndexTab, setcurrentQuestionIndexTab] = useState(0);
    const [AnswersQuestion, setAnswersQuestion] = useState([]);
    const [selectionFlag, setSelectionFlag] = useState(false);
    const [friendName, setfriendName] = useState([]);
    const [isRequestProceed, setIsRequestProceed] = useState(true);
    const [isLoading, SetisLoading] = useState(true)

    const paramss = useParams().challengeId;


    const OwnerFriendQuizaswerSubmitted = async (c_name, quiz_uid, quiz_data) => {
        if (isRequestProceed) {
            setIsRequestProceed(false)
            const response = await fetch(`${process.env.REACT_APP_BASE_URL}/acceptChallenge`, {
                method: 'POST',
                headers: {
                    'Content-Type': 'application/json'
                },
                body: (JSON.stringify({
                    "c_name": c_name,
                    "quiz_uid": quiz_uid,
                    "quiz_data": quiz_data
                }))
            })
            if (!response.ok) {
                throw new Error('Data coud not be fetched!')
            } else {
                return response.json()
            }
        }
    }

    const handleNextQuestion = (e) => {
        setTimeout(() => {
            setSelectionFlag(true);
        }, 200);
        if (currentQuestionIndexTab <= 20) {
            const splitanservalue = e.split(" ");
            currentQuestion[currentQuestion?.findIndex(x => x.q_id == splitanservalue[0])].isAnswered = true;
            currentQuestion[currentQuestion?.findIndex(x => x.q_id == splitanservalue[0])].friend_a_id = +splitanservalue[1];

            Promise.all(currentQuestion.filter(x => x.isAnswered !== true)[1]?.Answers?.map(choice =>
                loadImage(`${process.env.REACT_APP_BASE_URL}/public/${choice.a_thumb}`)))
                .then()
                .catch(err => console.log("Failed to load images", err))

            setTimeout(() => {
                setAnswersQuestion([...AnswersQuestion, currentQuestion?.filter(x => x.isAnswered === true)[0]].flat())
                setCurrentQuestion([...currentQuestion?.filter(x => x.isAnswered !== true)])
                setcurrentQuestionIndexTab(AnswersQuestion.length + 1);
                setSelectionFlag(false);
            }, 1000);
        }

    };

    const handleSkipQuestion = (e) => {
        setCurrentQuestion([currentQuestion.slice(1, currentQuestion.length), currentQuestion.slice(0, 1)].flat());
    };

    const fetchData = async () => {
        const response = await fetch(`${process.env.REACT_APP_BASE_URL}/getQuizWithQuestionsAnswers/${paramss}`)
        if (!response.ok) {
            throw new Error('Data coud not be fetched!')
        } else {
            return response.json()
        }
    }

    useEffect(() => {

        if (currentQuestion.length <= 0) {
            fetchData()
                .then((res) => {
                    setfriendName(res.quiz_performer)
                    setCurrentQuestion(res.questions)
                }).finally(() => {
                    SetisLoading(false)
                })
        }
        if (currentQuestionIndexTab >= 20) {

            OwnerFriendQuizaswerSubmitted(challengerNavigation.FriendNameChallengerName, paramss, AnswersQuestion.map((item) => ({ [item.q_id.toString()]: item.friend_a_id.toString() }))).then((res) => {
                if (res.message === "Quiz created successfully") {
                    handleChallengerClick({ "isAttemptQuizCompleted": true, "newQuizData": res.newQuizData, "AttemptQuizId": res.newQuizData.quiz_uid });
                }
            })
                .catch((e) => {
                    console.log(e)
                })
        }
    }, [AnswersQuestion]);

    const replaceProNounwithName = (text) => {
        return !!text && text.replaceAll('your', friendName + "'s").replaceAll('you', friendName)
    }
    const shuffleArray = (arr) => {
        if (arr) {
            const newArr = arr.slice()
            for (let i = newArr.length - 1; i > 0; i--) {
                const rand = Math.floor(Math.random() * (i + 1));
                [newArr[i], newArr[rand]] = [newArr[rand], newArr[i]];
            }
            return newArr
        }
    };

    let wrongSelectCard = new Audio(wrongSelect);
    let CorrectSelectCard = new Audio(CorrectSelect);

    const handleSound = (e) => {
        if (e) {
            wrongSelectCard.play()
        }
        else {
            CorrectSelectCard.play()
        }
    }

    const GetCardStatus = (e) => {
        if (e) {
            return 'card-active'
        }
        else {
            return 'card-active-wrong'
        }
    }

    return (
        <div className="container">
            <div className="col-md-8 col-sm-12 offset-md-2 mt-4">
                <div className="card-header card mt-3 px-3 shadow-sm">

                    <div className="countdown">
                        <ul className="p-0">
                            <li className={currentQuestionIndexTab === 0 ? "active" : ""}>1</li>
                            <li className={currentQuestionIndexTab === 1 ? "active" : ""}>2</li>
                            <li className={currentQuestionIndexTab === 2 ? "active" : ""}>3</li>
                            <li className={currentQuestionIndexTab === 3 ? "active" : ""}>4</li>
                            <li className={currentQuestionIndexTab === 4 ? "active" : ""}>5</li>
                            <li className={currentQuestionIndexTab === 5 ? "active" : ""}>6</li>
                            <li className={currentQuestionIndexTab === 6 ? "active" : ""}>7</li>
                            <li className={currentQuestionIndexTab === 7 ? "active" : ""}>8</li>
                            <li className={currentQuestionIndexTab === 8 ? "active" : ""}>9</li>
                            <li className={currentQuestionIndexTab === 9 ? "active" : ""}>10</li>
                            <li className={currentQuestionIndexTab === 10 ? "active" : ""}>11</li>
                            <li className={currentQuestionIndexTab === 11 ? "active" : ""}>12</li>
                            <li className={currentQuestionIndexTab === 12 ? "active" : ""}>13</li>
                            <li className={currentQuestionIndexTab === 13 ? "active" : ""}>14</li>
                            <li className={currentQuestionIndexTab === 14 ? "active" : ""}>15</li>
                            <li className={currentQuestionIndexTab === 15 ? "active" : ""}>16</li>
                            <li className={currentQuestionIndexTab === 16 ? "active" : ""}>17</li>
                            <li className={currentQuestionIndexTab === 17 ? "active" : ""}>18</li>
                            <li className={currentQuestionIndexTab === 18 ? "active" : ""}>19</li>
                            <li className={currentQuestionIndexTab === 19 ? "active" : ""}>20</li>
                        </ul>
                    </div>

                    <div className="mb-3 text-center">
                        <button className="bg-danger btn text-white border-radius-100 p-2" value="0" onClick={!selectionFlag ? handleSkipQuestion : null}>Skip This Question</button>
                    </div>

                    {!isLoading && currentQuestion && <div className="text-center">
                        <h4>{replaceProNounwithName(currentQuestion[0]?.q_title)}</h4>
                        <div className="row" >
                            {shuffleArray(currentQuestion && currentQuestion[0]?.Answers)?.map((choice, i) => (
                                <div className='col-6 mb-3  cursor-grabbing  cursor-grab mt-5' key={i} onClick={!selectionFlag ? () => handleNextQuestion(currentQuestion[0].q_id + " " + choice?.a_id) : null}  >
                                    <div onClick={!selectionFlag ? () => handleSound(choice?.a_id === +currentQuestion[0].owner_a_id) : null}
                                        className='border-radius-10 border-1-grey card border-radius-10 shadow position-relative'>
                                        <div className={selectionFlag ? GetCardStatus(choice?.a_id === +currentQuestion[0].owner_a_id) : ''}>
                                            <div className='card-body-img '>
                                                <img src={`${process.env.REACT_APP_BASE_URL}/public/${choice?.a_thumb}`} alt={choice?.a_text} />
                                            </div>
                                            <p className="fw-bolder p-0 w-100" >
                                                {choice.a_text}
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                    }
                    {isLoading &&
                        <div className="text-center my-4">
                            <div className="spinner-border text-primary" role="status">

                            </div>
                            <div>
                                Is Loading...
                            </div>
                        </div>
                    }
                </div>
            </div>
        </div>
    );
}

export default FriendQuizContainer;
