import React, { useContext } from 'react';
import stop from '../../Assets/stop.png';
import { DareRootContext } from '../../App';
import { CreateAttempterNewQuiz } from '../../utilities/setLocalstorage';
import { useNavigate } from 'react-router';

function QuizDeleted() {
    const { siteData } = useContext(DareRootContext);
    const navigate = useNavigate();
console.log(siteData)
    const createOwnQuiz = () => {
        if (CreateAttempterNewQuiz()) {
            navigate('/');
        }
        else{
            navigate('/');
        }
    }

    return (

        <div className="container">
            <div className="col-md-6 offset-md-3 mt-5">
                <div className="card  mt-3">
                    <div className="card-body card-header p-4 shadow text-center">
                        <div>
                            <img
                                className="max-width-150 img-thumbnail"
                                src={`${process.env.REACT_APP_BASE_URL}/public/${siteData?.site_og_image}`}
                                alt={`${siteData?.site_og_image}`}
                            />
                        </div>

                        <div>
                            <img className='max-width-150 my-2' alt='Delete Img' src={stop} />
                        </div>

                        <h6>The Quiz has been deleted by the owner. You can create your own by clicking the below button.</h6>

                        <div className="mt-3">
                            👇👇👇👇👇👇👇👇👇👇👇👇<br />
                            👉<a href={siteData?.site_wishing_web} target="_blank">
                                Touch this blue line and see magic
                            </a>👈
                            <br />☝☝☝☝☝☝☝☝☝☝☝☝
                        </div>

                        <a onClick={createOwnQuiz} className="btn btn-warning mt-2 p-1">Create Your Own Quiz</a>
                    </div>
                </div>

            </div>
        </div>

    )
}
export default QuizDeleted;
