import { useState, useCallback, useContext, useEffect } from 'react';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import { DeleteOwnerQuizChallenge } from '../../utilities/setLocalstorage';
import { DareClientContext, DareRootContext } from '../../App'
import { GetImageByQuizID } from '../../utilities/setLocalstorage';
import whatsapp from '../../Assets/whatsapp-logo.png';
import twitter from '../../Assets/twitter.png';
import line from '../../Assets/line.png';
import talk from '../../Assets/talk.png';
import messanger from '../../Assets/messenger.png';
import vk from '../../Assets/vk.png';
import fb from '../../Assets/fb.png';

import { useNavigate } from 'react-router';
function OwnerCompeletionCard() {

    const navigate = useNavigate();
    const { handleRootClick } = useContext(DareRootContext);
    const { naviagation, categorylist } = useContext(DareClientContext);
    const [copied, setCopied] = useState(false);
    const [location, setlocation] = useState("");
    const [currentId, setcurrentId] = useState("");
    const [siteData, setSiteData] = useState([]);

    const DeleteUserData = async () => {
        // DeleteOwnerQuizChallenge(currentId)
        await fetch(`${process.env.REACT_APP_BASE_URL}/deleteQuiz/${currentId}`, {
            method: 'GET',
            headers: { 'Content-Type': 'application/json' }
        }).then((ea) => {
            handleRootClick({ "toast": `Quiz ID ${currentId} Deleted Successfully` });
            if (DeleteOwnerQuizChallenge(currentId)) {
                navigate('/en/');
            }
        }).catch((e) => {
            navigate('/en/');
        })
    }

    const onCopy = useCallback(() => {
        setCopied(true);
        handleRootClick({ "toast": "Copied" });
    }, [])

    useEffect(() => {
        // console.log(naviagation)
        const siteDataLocal = JSON.parse(localStorage.getItem("siteData"));
        setSiteData(siteDataLocal[0]);
        // console.log(naviagation?.OwnerQuizs.length)
        setcurrentId(naviagation?.OwnerQuizs[naviagation?.OwnerQuizs.length - 1]?.OwnerQuizId)
        setlocation(window.location.origin + '/en/quiz/' + naviagation?.OwnerQuizs[naviagation?.OwnerQuizs.length - 1]?.OwnerQuizId);
    }, [location]);

    return (
        <div className="App">
            <div className="container-sm">
                <div className="card  col-md-8 offset-md-2 mt-5">
                    <div className="container card-header shadow text-center py-3">
                        {categorylist && categorylist.length > 0 && <img
                            className="max-width-150 img-thumbnail"
                            src={`${categorylist?.findIndex(x => x.t_id === naviagation.quizType) > -1 ? process.env.REACT_APP_BASE_URL + '/public/' + categorylist[categorylist?.findIndex(x => x.t_id == naviagation.quizType)].t_image : process.env.REACT_APP_BASE_URL + '/public/' + categorylist[0]?.t_image}`}
                            alt={`${naviagation?.quizType}`}
                        />}

                        <h3 className="text-danger">Your Challenge is Ready</h3>
                        <h5>Share this link with your friends</h5>
                        <div className="container mt-2">
                            <div className='row'>
                                <div className='col-lg-8 col-sm-12 px-0'>
                                    <input className="border btn shadow w-100" type="input" readOnly value={location} />
                                </div>
                                <div className='col-lg-4 col-sm-12 mt-2 mt-lg-0 px-0'>
                                    <CopyToClipboard onCopy={onCopy} text={location}>
                                        <button className="bg-primary btn  text-white w-100 copy-btn">
                                            Copy the Link
                                            <svg className='ms-2 img-15' style={{ fill: "#ffffff" }}
                                                version="1.1" id="Layer_1" x="0px" y="0px"
                                                viewBox="0 0 115.77 122.88"
                                            ><g><path
                                                d="M89.62,13.96v7.73h12.19h0.01v0.02c3.85,0.01,7.34,1.57,9.86,4.1c2.5,2.51,4.06,5.98,4.07,9.82h0.02v0.02 v73.27v0.01h-0.02c-0.01,3.84-1.57,7.33-4.1,9.86c-2.51,2.5-5.98,4.06-9.82,4.07v0.02h-0.02h-61.7H40.1v-0.02 c-3.84-0.01-7.34-1.57-9.86-4.1c-2.5-2.51-4.06-5.98-4.07-9.82h-0.02v-0.02V92.51H13.96h-0.01v-0.02c-3.84-0.01-7.34-1.57-9.86-4.1 c-2.5-2.51-4.06-5.98-4.07-9.82H0v-0.02V13.96v-0.01h0.02c0.01-3.85,1.58-7.34,4.1-9.86c2.51-2.5,5.98-4.06,9.82-4.07V0h0.02h61.7 h0.01v0.02c3.85,0.01,7.34,1.57,9.86,4.1c2.5,2.51,4.06,5.98,4.07,9.82h0.02V13.96L89.62,13.96z M79.04,21.69v-7.73v-0.02h0.02 c0-0.91-0.39-1.75-1.01-2.37c-0.61-0.61-1.46-1-2.37-1v0.02h-0.01h-61.7h-0.02v-0.02c-0.91,0-1.75,0.39-2.37,1.01 c-0.61,0.61-1,1.46-1,2.37h0.02v0.01v64.59v0.02h-0.02c0,0.91,0.39,1.75,1.01,2.37c0.61,0.61,1.46,1,2.37,1v-0.02h0.01h12.19V35.65 v-0.01h0.02c0.01-3.85,1.58-7.34,4.1-9.86c2.51-2.5,5.98-4.06,9.82-4.07v-0.02h0.02H79.04L79.04,21.69z M105.18,108.92V35.65v-0.02 h0.02c0-0.91-0.39-1.75-1.01-2.37c-0.61-0.61-1.46-1-2.37-1v0.02h-0.01h-61.7h-0.02v-0.02c-0.91,0-1.75,0.39-2.37,1.01 c-0.61,0.61-1,1.46-1,2.37h0.02v0.01v73.27v0.02h-0.02c0,0.91,0.39,1.75,1.01,2.37c0.61,0.61,1.46,1,2.37,1v-0.02h0.01h61.7h0.02 v0.02c0.91,0,1.75-0.39,2.37-1.01c0.61-0.61,1-1.46,1-2.37h-0.02V108.92L105.18,108.92z" /></g></svg></button>
                                    </CopyToClipboard>
                                </div>
                            </div>
                        </div>

                        <div className="mt-2 row">
                            <div className="col-md-12 mb-2">
                                {
                                    siteData &&
                                    <a className="share-btn btn-whatsapp border-radius-100 w-100"
                                        href={`whatsapp://send?text=😍${naviagation.quizOwnerName} ${siteData?.site_description} ${location}`}
                                        target="_blank">
                                        <div className="brand-icon"><img src={whatsapp} alt="whatsapp img" />
                                        </div><h6>Whatsapp</h6>
                                    </a>
                                }
                            </div>

                            <div className="col-md-6 mb-2">
                                <a href={`https://www.snapchat.com/scan?attachmentUrl=${location}`} className="text-black share-btn btn border-radius-100 btn-snapchat w-100" target="_blank">
                                    <div className="brand-icon">  <img className="mr-2" src="https://quiz.quotedtale.com/assets/images/sc17.png" alt="snapchat" /></div> <h6 >Snapchat </h6>
                                </a>
                            </div>

                            <div className="col-md-6 mb-2">
                                <a href={`fb-messenger://share?link=${location}`} className="btn-messagner share-btn border-radius-100 w-100" target="_blank">
                                    <div className="brand-icon">   <img className="mr-2" src={messanger} alt="messanger" /></div><h6 >Messenger</h6>
                                </a>
                            </div>
                            <div className="col-md-12 mb-2">

                                <a href={`https://www.facebook.com/sharer/sharer.php?u=${location}`} className="btn-facebook share-btn border-radius-100 w-100" target="_blank">
                                    <div className="brand-icon">   <img src={fb} alt="fb" /></div><h6 >Messenger</h6>
                                </a>
                            </div>
                            <div className="col-md-6 mb-2">
                                <a className="border-radius-100 btn-twitter w-100 share-btn" href={`http://twitter.com/intent/tweet?text=${naviagation.quizOwnerName} has sent you  ${siteData?.site_title} of ${new Date().getFullYear()} 👸🤴. Take this Challenge NOW 🤯👇👇👇👇🤯 ${location}`} target="_blank">
                                    <div className="brand-icon"><img src={twitter} alt="twitter img" />  </div><h6>Share</h6>
                                </a>
                            </div>

                            <div className="col-md-6 mb-2">
                                <a className="btn-line share-btn border-radius-100 w-100"
                                    href={`line://msg/text/?😍${naviagation.quizOwnerName} ${siteData?.site_description} ${location}`} target="_blank">
                                    <div className="brand-icon"><img src={line} alt="whatslineapp img" />  </div><h6>Share</h6>
                                </a>
                            </div>
                            {/* <div className="col-md-6 mb-2">
                                <a className="btn-snapchat text-black share-btn border-radius-100 w-100" href={`whatsapp://send?text=😍${naviagation.quizOwnerName} ${siteData?.site_description} ${location}`} target="_blank">
                                    <div className="brand-icon"><img src={talk} alt="talk img" />  </div><h6>Share</h6>
                                </a>
                            </div>
                            <div className="col-md-6 mb-2">
                                <a className="btn-vk share-btn border-radius-100 w-100" href={`whatsapp://send?text=😍${naviagation.quizOwnerName} ${siteData?.site_description} ${location}`} target="_blank">
                                    <div className="brand-icon"><img src={vk} alt="vk img" />  </div><h6>Share</h6>
                                </a>
                            </div> */}
                        </div>


                        <button type="button" className="btn btn-instagram btn-primary border-radius-100 mt-2 mt-md-0" data-bs-toggle="modal" data-bs-target="#instGramModel">
                            <img className="mr-2" src="https://quiz.quotedtale.com/assets/images/in17.png" alt="sd" /> <span className="ms-1">Add to Instagram Bio </span>
                        </button>

                        <div className="modal fade" id="instGramModel" tabIndex="-1" aria-labelledby="exampleModalLabel" aria-hidden="true">
                            <div className="modal-dialog">
                                <div className="modal-content">
                                    <div className="modal-header">
                                        <h5 className="modal-title" id="exampleModalLabel">How to add this link to your
                                            Instagram Bio.</h5>
                                        <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Close"></button>
                                    </div>

                                    <div className="modal-body">
                                        <form>
                                            <div className="form-group">
                                                <label htmlFor="recipient-name" className="col-form-label">Copy Your Link:</label>
                                                <input readOnly type="text" className="form-control" value={location} />
                                            </div>
                                            <div className="form-group">
                                                <label htmlFor="message-text" className="col-form-label">Steps to Follow:</label>
                                                <ol className="list-group list-group-numbered text-start">
                                                    <li className="list-group-item">Copy your link</li>
                                                    <li className="list-group-item">Go on your profile in the app</li>
                                                    <li className="list-group-item">Click on Edit Profile</li>
                                                    <li className="list-group-item">Paste the link under Website section</li>
                                                </ol>
                                            </div>
                                        </form>
                                    </div>

                                    <div className="modal-footer">
                                        <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Close</button>
                                        <a href="https://instagram.com">
                                            <button type="button" className="btn btn-primary">Go to Instagram </button> </a>
                                    </div>

                                </div>
                            </div>
                        </div>


                        <div className="text-center mt-2">
                            <a className="btn btn-warning border-radius-100 btn-block w-100" href={location}>View Your Scoreboard</a>
                        </div>

                        <div className="mt-3">
                            <button className='bg-warning btn w-100' onClick={DeleteUserData}>You can delete this quiz by clicking here</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default OwnerCompeletionCard;