
import React, { useState, createContext, useEffect } from 'react';
import { Route, Routes } from 'react-router-dom';
import './App.css';

import { ToastContainer, toast } from 'react-toastify';
import Footer from './components/Common/Footer';
import AboutUs from './components/Public/AboutUs';
import Privacy from './components/Public/Privacy';
import TermsAndCondition from './components/Public/TermsAndCondition';
import Header from './components/Common/Header';

import AdminLayout from './components/Admin/AdminLayout';
import NoPage from './components/Common/NoPage';
import ChallengeCards from './components/Dashboard/ChallengeCards'
import ClientLayout from './components/Client/ClientLayout';
import FriendLayout from './components/FriendChallenges/FriendLayout';
import OwnerNameCard from './components/Client/OwnerNameCard';
import OwnerQuizContainer from './components/Client/OwnerQuizContainer';
import { Helmet } from 'react-helmet';
import { useNavigate } from 'react-router';
import { stringToBoolean, setLocalStorage, loadImage } from './utilities/setLocalstorage';



export const DareRootContext = createContext();
export const DareClientContext = createContext();


function App() {

  const navigate = useNavigate();
  const [siteData, setSiteData] = useState({});

  const restOwnerQuiz = {
    "OwnerQuizs": [],
    "isOwnerQuestionCompleted": false,
    "quizOwnerName": "",
  }

  const OwnerQuizItems = localStorage.getItem("OwnerQuizItems") !== null ? JSON.parse(localStorage.getItem("OwnerQuizItems")) : localStorage.setItem("OwnerQuizItems", JSON.stringify(restOwnerQuiz));
  // console.log(OwnerQuizItems)
  const [naviagation, setNavigation] = useState({ ...OwnerQuizItems });

  const fetchSiteData = async () => {
    const response = await fetch(`${process.env.REACT_APP_BASE_URL}/getsite`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
      }
    })
    // console.log(response)
    if (!response.ok) {
      throw new Error('Data coud not be fetched!')
    } else {
      return response.json()
    }
  }

  const [categorylist, setCategoryList] = useState();

  const fetchDataList = () => {
    const fetchData = async () => {
      const response = await fetch(`${process.env.REACT_APP_BASE_URL}/getAllTypes`,
        { method: "GET" });
      if (!response.ok) {
        throw new Error('Data coud not be fetched!')
      } else {
        return response.json()
      }
    }

    fetchData().then((res) => {
      setCategoryList(res);
      // setFilterData(res);
      // console.log(res)
    }).catch(
      er => {
        console.log(er)
      }
    )
  }

  useEffect(() => {
    if (OwnerQuizItems) {
      setNavigation({
        ...naviagation,
        "quizOwnerName": OwnerQuizItems?.quizOwnerName ? OwnerQuizItems?.quizOwnerName : '',
        "isOwnerQuestionCompleted": stringToBoolean(OwnerQuizItems?.isOwnerQuestionCompleted),
        "OwnerQuizs": OwnerQuizItems?.OwnerQuizs ? [...OwnerQuizItems?.OwnerQuizs] : [],
        "quizType": OwnerQuizItems?.quizType,
      });
    }

    fetchSiteData().then(e => {
      setSiteData(e[0])
      localStorage.setItem("siteData", JSON.stringify(e));
      if (!!e[0]?.site_og_image) {
        loadImage(`${process.env.REACT_APP_BASE_URL}/public/${e[0]?.site_og_image}`);
      }
    })
    fetchDataList();
  }, [OwnerQuizItems?.quizOwnerName, OwnerQuizItems?.isOwnerQuestionCompleted, OwnerQuizItems?.OwnerQuizId])

  const handleRootClick = (e) => {
    if (e.hasOwnProperty("toast")) {
      toast(e.toast)
    }
  }

  const handleUserClick = (e) => {
    if (e.hasOwnProperty("quizType")) {
      setNavigation({ ...naviagation, "quizType": e.quizType });
      console.log(naviagation)
      setLocalStorage({ ...naviagation, "quizType": e.quizType })
      navigate('/en/')
    }


    if (e.hasOwnProperty("quizOwnerName")) {
      setNavigation({ ...naviagation, "quizOwnerName": e.quizOwnerName });
      navigate('/en/quiz')
    }

    if (e.hasOwnProperty("isOwnerQuestionCompleted")) {
      const a = { ...naviagation };
      const { OwnerQuizs, ...copiedAndExcluded } = a;
      setNavigation({ ...copiedAndExcluded, "isOwnerQuestionCompleted": stringToBoolean(e.isOwnerQuestionCompleted), "OwnerQuizs": [...e.OwnerQuizs] });
    }
  }


  return (
  <div>

    <DareRootContext.Provider value={{ siteData, handleRootClick }}>

      <Helmet>
        <title>{siteData?.site_title}</title>
        <link rel="icon" href={process.env.REACT_APP_BASE_URL + '/public/' + siteData?.site_og_image} />
        <link rel="icon" sizes="16x16" type="image/png" href={`${process.env.REACT_APP_BASE_URL}/public/${siteData?.site_og_image}`} />
        <meta name="description" content={siteData?.site_title} />
        <meta property="og:image" content={process.env.REACT_APP_BASE_URL + '/public/' + siteData?.site_og_image}></meta>
        <link rel="apple-touch-icon" href={process.env.REACT_APP_BASE_URL + '/public/' + siteData?.site_og_image} />
      </Helmet>

      <DareClientContext.Provider value={{ naviagation, handleUserClick, siteData, categorylist }}>
        <Header />
        <Routes>

          <Route exact path="/" element={<ChallengeCards />} />

          <Route exact path="en/" element={<ClientLayout />} >
            <Route exact index element={<OwnerNameCard />} />
            <Route exact path="quiz" element={<OwnerQuizContainer />} />
          </Route>

          <Route exact path="en/quiz/:challengeId" element={<FriendLayout />} />

          <Route exact path="admin/*" element={<AdminLayout />} />
          <Route exact path="aboutus" element={<AboutUs />} />
          <Route exact path="privacy" element={<Privacy />} />
          <Route exact path="termsandcondition" element={<TermsAndCondition />} />

          
          <Route path="*" element={<NoPage />} />

        </Routes>
<Footer/>
      </DareClientContext.Provider>

    </DareRootContext.Provider>
    <ToastContainer />

  </div >
  );
}

export default App;
