import React, { useContext, useState, useEffect } from 'react';
import { useNavigate } from "react-router-dom";
import Introduction from '../Common/Introduction'
import { DareClientContext } from '../../App';
import { DeleteOwnerQuizChallenge } from '../../utilities/setLocalstorage';

import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';

import friendshipdare from '../../Assets/friendshipdare.png';
import lovedare from '../../Assets/lovedare.png';
import foodchallenge from '../../Assets/foodchallenge.png';
import sisterquiz from '../../Assets/sisterquiz.png';
import superdare from '../../Assets/superdare.png';
import top5dare from '../../Assets/top5dare.png';
import truefalse from '../../Assets/truefalse.png';


function ChallengeCards() {

    const { naviagation, handleRootClick, handleUserClick, categorylist } = useContext(DareClientContext);
    const [ShowEditAnswer, setShowEditAnswer] = useState();
    const [quizType, setquizType] = useState();

    const handleCloseEdit = () => setShowEditAnswer(false);
    const navigate = useNavigate();

    const handleChallengeClick = (event) => {
        setquizType(event?.t_id)
        console.log()
        if (naviagation?.OwnerQuizs.some(x => x.quizType === event?.t_id)) {
            setShowEditAnswer(true)
        }
        else {
            navigate('/en/')
            handleUserClick({ "quizType": event?.t_id })
        }
    };

    const DelQuizItem = async () => {
        const response = await fetch(`${process.env.REACT_APP_BASE_URL}/deleteQuiz/${naviagation?.OwnerQuizId}`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            }
        })
        if (!response.ok) {
            throw new Error('Data could not be fetched!')
        } else {
            return response.json()
        }

    };

    const handleResponce = (e) => {
        if (e) {
            handleUserClick({ "quizType": quizType })
            setShowEditAnswer(false)
            DelQuizItem().then(e => {
                handleRootClick({ "toast": `Quiz ID ${naviagation?.OwnerQuizId} deleted successfully` })
                DeleteOwnerQuizChallenge(naviagation?.OwnerQuizId)
                setTimeout(() => {
                    navigate('/en/');
                }, 500);
            }
            ).catch((eas) => {
                DeleteOwnerQuizChallenge(naviagation?.OwnerQuizId)
                setTimeout(() => {
                    navigate('/en/');
                }, 500);
            })
        }
        else {
            // setTimeout(() => {
            handleUserClick({ "quizType": quizType })
            navigate('/en/');
            // }, 500);
        }
    }



    // useEffect(() => {
    //     // console.log(naviagation)
    // }, [])



    return (<>
        <div className='bg-body-secondary'>

            <div className="container ">
                <div className='row pt-3'>
                    <h1 className='text-center fw-bold text-primary-emphasis'>Create Dare Quiz</h1>

                    {categorylist?.length > 0 && categorylist.map((e, i) => (
                        <div className='col-lg-3 col-md-6' key={i}>
                            <div className='shadow card p-3 my-2 cursor-grap'>
                                <div className="text-center" onClick={() => handleChallengeClick(e)}>
                                    <img src={`${process.env.REACT_APP_BASE_URL}/public/${e?.t_image}`} alt="Besti" className="max-width-150" />
                                    <h4 className='text-primary mt-2'>
                                        {e.t_title}
                                    </h4>
                                </div>
                            </div>
                        </div>
                    ))
                    }

                    {/* <div className='col-lg-3 col-md-6'>
                        <div className='shadow card p-3 my-2 cursor-grap'>
                            <div className="text-center" onClick={() => handleChallengeClick(1)}>
                                <img src={friendshipdare} alt="Besti" className="max-width-150" />
                                <h4 className='text-primary'>
                                    Friendship Dare {new Date().getFullYear()} Quiz
                                </h4>
                                <button className="bg-danger border-radius-0 text-white" value={1} >
                                    Start
                                </button>
                            </div>
                        </div>

                    </div> */}

                    {/* <div className='col-lg-3 col-md-6'>
                        <div className='shadow card p-3 my-2 cursor-grap'>
                            <div className="text-center" onClick={() => handleChallengeClick(2)}>
                                <img src={lovedare} alt="Besti" className="max-width-150" />
                                <h4 className='text-primary'>Love Challenge Quiz</h4>
                                <button className="bg-danger border-radius-0 text-white " >
                                    Start
                                </button>
                            </div>
                        </div>

                    </div> */}

                    {/* <div className='col-lg-3 col-md-6'>
                        <div className='shadow card p-3 my-2 cursor-grap'>
                            <div className="text-center" onClick={() => handleChallengeClick(3)}>
                                <img src={foodchallenge} alt="Besti" className="max-width-150" />
                                <h4 className='text-primary'>
                                    Food Dare Challenge {new Date().getFullYear()}
                                </h4>
                                <button className="bg-danger border-radius-0 text-white" >
                                    Start
                                </button>
                            </div>
                        </div>
                    </div> */}

                    {/* <div className='col-lg-3 col-md-6'>
                        <div className='shadow card p-3 my-2 cursor-grap'>
                            <div className="text-center" onClick={() => handleChallengeClick(4)}>
                                <img src={sisterquiz} alt="Besti" className="max-width-150" />
                                <h4 className='text-primary'>
                                    Sister Dare Quiz
                                </h4>
                                <button className="bg-danger border-radius-0 text-white" >
                                    Start
                                </button>
                            </div>
                        </div>
                    </div> */}
                    {/* 
                    <div className='col-lg-3 col-md-6'>
                        <div className='shadow card p-3 my-2 cursor-grap'>
                            <div className="text-center" onClick={() => handleChallengeClick(5)}>
                                <img src={superdare} alt="Besti" className="max-width-150" />
                                <h4 className='text-primary'>
                                    Super Dare
                                </h4>
                                <button className="bg-danger border-radius-0 text-white" >
                                    Start
                                </button>
                            </div>
                        </div>
                    </div> */}

                    {/* <div className='col-lg-3 col-md-6'>
                        <div className='shadow card p-3 my-2 cursor-grap'>
                            <div className="text-center" onClick={() => handleChallengeClick(6)}>
                                <img src={truefalse} alt="Besti" className="max-width-150" />
                                <h4 className='text-primary'>
                                    Truth or False Quiz
                                </h4>
                                <button className="bg-danger border-radius-0 text-white" >
                                    Start
                                </button>
                            </div>
                        </div>
                    </div> */}

                    {/* <div className='col-lg-3 col-md-6'>
                        <div className='shadow card p-3 my-2 cursor-grap'>
                            <div className="text-center" onClick={() => handleChallengeClick(7)}>
                                <img src={top5dare} alt="Besti" className="max-width-150" />
                                <h4 className='text-primary'>
                                    Top 5 Friends Challenge
                                </h4>
                                <button className="bg-danger border-radius-0 text-white" >
                                    Start
                                </button>
                            </div>
                        </div>
                    </div> */}

                </div>
                <Introduction />
            </div >


            <div>
                <Modal
                    show={ShowEditAnswer}
                    onHide={handleCloseEdit}
                    backdrop="static"
                    keyboard={false}
                >
                    <Modal.Header closeButton>
                        <Modal.Title><h6>Confirmation </h6> </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <p>A question is already created against this Quiz. Do you want to create new one?</p>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={() => handleResponce(false)}>
                            No
                        </Button>
                        <Button variant="primary" onClick={() => handleResponce(true)}>
                            Yes
                        </Button>
                    </Modal.Footer>
                </Modal>

            </div>
        </div >
    </>
    );
}

export default ChallengeCards;