import React, { useContext, useCallback, useEffect, useState } from 'react';
import { DeleteOwnerQuizChallenge, IsOwnNewChallenge } from '../../utilities/setLocalstorage';
import { DareChallengerContext } from './FriendLayout'
import { DareClientContext } from '../../App';
import { CopyToClipboard } from 'react-copy-to-clipboard';

import whatsapp from '../../Assets/whatsapp-logo.png';
import winner1 from '../../Assets/QuizWinnerCategory-1.png';
import winner2 from '../../Assets/QuizWinnerCategory-2.png';
import winner3 from '../../Assets/QuizWinnerCategory-3.png';
import fb from '../../Assets/fb.png'
import messenger from '../../Assets/messenger.png'

import { useNavigate } from 'react-router';
function OwnerChallengeScoreCard() {
    const navigate = useNavigate();

    const { categorylist } = useContext(DareClientContext);
    const { challengerNavigation, naviagation, getScored, params, siteData, handleRootClick } = useContext(DareChallengerContext);
    const [Copied, setCopied] = useState(false);
    const [locations, setlocation] = useState("");

    const onCopy = useCallback(() => {
        setCopied(true);
        handleRootClick({ "toast": "Copied" });
    }, [])

    const DelQuizItem = async () => {
        const response = await fetch(`${process.env.REACT_APP_BASE_URL}/deleteQuiz/${params?.challengeId}`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
            }
        })
        if (!response.ok) {
            throw new Error('Data could not be fetched!')
        } else {
            return response.json()
        }

    }
    const DeleteUserData = async () => {
        if (IsOwnNewChallenge(params?.challengeId)) {
            DelQuizItem().then(e => {
                handleRootClick({ "toast": `Quiz ID ${params?.challengeId} deleted successfully` });
                DeleteOwnerQuizChallenge(params?.challengeId)
                setTimeout(() => {
                    navigate('/en/');
                }, 500);
            }
            ).catch((eas) => { })
        }
    }


    useEffect(() => {
        console.log(categorylist)
        console.log(naviagation)
        setlocation(`${window.location.origin + '/en/quiz/' + params?.challengeId}`)
    }, []);


    return (
        <div className="container">
            <div className="row">

                <div className="col-md-8 offset-md-2 mt-3">
                    <div className="row">
                        <div className='col-md-12'>

                            <div className="card-body card-header card shadow text-center">

                               
                                 
                                
                             
                            
                                <img
                                    className="img-thumbnail img-fluid mb-2 max-width-150 mx-auto"
                                    src={`${process.env.REACT_APP_BASE_URL}/public/${categorylist && categorylist[categorylist.findIndex(x => (x.t_id === naviagation.quizType))].t_image}`}
                                    alt={`${siteData?.site_og_image}`}
                                />
                                <h4>Your Challenge is Ready</h4>
                                <p>Share this link with your friends</p>

                                <div className="row">
                                    <div className='col-lg-8 col-sm-12 pe-lg-0'>
                                        <input className="border btn shadow w-100" type="input" readOnly value={locations} />
                                    </div>

                                    <div className='col-lg-4 col-sm-12 mt-2 mt-lg-0 ps-lg-0'>
                                        <CopyToClipboard onCopy={onCopy} text={locations}>
                                            <button className="bg-primary btn  text-white w-100 copy-btn">
                                                Copy the Link
                                                <svg className='ms-2 img-15' style={{ fill: "#ffffff" }}
                                                    version="1.1" id="Layer_1" x="0px" y="0px"
                                                    viewBox="0 0 115.77 122.88"
                                                ><g><path
                                                    d="M89.62,13.96v7.73h12.19h0.01v0.02c3.85,0.01,7.34,1.57,9.86,4.1c2.5,2.51,4.06,5.98,4.07,9.82h0.02v0.02 v73.27v0.01h-0.02c-0.01,3.84-1.57,7.33-4.1,9.86c-2.51,2.5-5.98,4.06-9.82,4.07v0.02h-0.02h-61.7H40.1v-0.02 c-3.84-0.01-7.34-1.57-9.86-4.1c-2.5-2.51-4.06-5.98-4.07-9.82h-0.02v-0.02V92.51H13.96h-0.01v-0.02c-3.84-0.01-7.34-1.57-9.86-4.1 c-2.5-2.51-4.06-5.98-4.07-9.82H0v-0.02V13.96v-0.01h0.02c0.01-3.85,1.58-7.34,4.1-9.86c2.51-2.5,5.98-4.06,9.82-4.07V0h0.02h61.7 h0.01v0.02c3.85,0.01,7.34,1.57,9.86,4.1c2.5,2.51,4.06,5.98,4.07,9.82h0.02V13.96L89.62,13.96z M79.04,21.69v-7.73v-0.02h0.02 c0-0.91-0.39-1.75-1.01-2.37c-0.61-0.61-1.46-1-2.37-1v0.02h-0.01h-61.7h-0.02v-0.02c-0.91,0-1.75,0.39-2.37,1.01 c-0.61,0.61-1,1.46-1,2.37h0.02v0.01v64.59v0.02h-0.02c0,0.91,0.39,1.75,1.01,2.37c0.61,0.61,1.46,1,2.37,1v-0.02h0.01h12.19V35.65 v-0.01h0.02c0.01-3.85,1.58-7.34,4.1-9.86c2.51-2.5,5.98-4.06,9.82-4.07v-0.02h0.02H79.04L79.04,21.69z M105.18,108.92V35.65v-0.02 h0.02c0-0.91-0.39-1.75-1.01-2.37c-0.61-0.61-1.46-1-2.37-1v0.02h-0.01h-61.7h-0.02v-0.02c-0.91,0-1.75,0.39-2.37,1.01 c-0.61,0.61-1,1.46-1,2.37h0.02v0.01v73.27v0.02h-0.02c0,0.91,0.39,1.75,1.01,2.37c0.61,0.61,1.46,1,2.37,1v-0.02h0.01h61.7h0.02 v0.02c0.91,0,1.75-0.39,2.37-1.01c0.61-0.61,1-1.46,1-2.37h-0.02V108.92L105.18,108.92z" /></g></svg></button>
                                        </CopyToClipboard>
                                    </div>


                                    <div className="col-md-12 my-2">
                                        <a className="share-btn btn-whatsapp border-radius-100 w-100"
                                            href={`whatsapp://send?text=😍 ${!!challengerNavigation?.quizOwnerName ? challengerNavigation?.quizOwnerName : ''} ${siteData?.site_description} ${locations}`}
                                            target="_blank">
                                            <div className="brand-icon"><img src={whatsapp} alt="whatsapp img" />
                                            </div><h6>Whatsapp</h6>
                                        </a>
                                    </div>

                                    <div className="col-md-6 mb-2">
                                        <a href={`https://www.snapchat.com/scan?attachmentUrl=${locations}`} className="text-black share-btn btn border-radius-100 btn-snapchat w-100" target="_blank">
                                            <div className="brand-icon">  <img className="mr-2" src="https://quiz.quotedtale.com/assets/images/sc17.png" alt="snapchat" /></div> <h6 >Snapchat </h6>
                                        </a>
                                    </div>

                                    <div className="col-md-6 mb-2">
                                        <a href={`fb-messenger://share?link=${locations}`} className="btn-messagner share-btn border-radius-100 w-100" target="_blank">
                                            <div className="brand-icon">   <img className="mr-2" src={messenger} alt="messanger" /></div><h6 >Messenger</h6>
                                        </a>
                                    </div>

                                    <div className="col-md-12 mb-2">

                                        <a href={`https://www.facebook.com/sharer/sharer.php?u=${locations}`} className="btn-facebook share-btn border-radius-100 w-100" target="_blank">
                                            <div className="brand-icon">   <img src={fb} alt="fb" /></div><h6 >Messenger</h6>
                                        </a>
                                    </div>


                                </div>
                                <div className="mt-3">
                                    👇👇👇👇👇👇👇👇👇👇👇👇<br />
                                    👉<a href={siteData?.site_wishing_web} target="_blank">
                                        Touch this blue line and see magic
                                    </a>👈
                                    <br />☝☝☝☝☝☝☝☝☝☝☝☝
                                </div>

                                <div className="mt-3">
                                    <button className='bg-warning btn w-100' onClick={DeleteUserData}>You can delete this quiz by clicking here</button>
                                </div>
                            </div>
                        </div>
                    </div>



                    <div className="mt-3 text-center">




                        <h5>Who Knows you Best?</h5>
                        {/* 
                        <div className='row'>
                            <div className='col-4'><img src={winner2} width='35px' /></div>
                            <div className='col-4'><img src={winner1} width='50px' /></div>
                            <div className='col-4'><img src={winner3} width='35px' /></div>
                        </div> */}


                        <table className="table table-bordered table-striped">
                            <thead >
                                <tr>
                                    <th className="bg-danger text-white">Name</th>
                                    <th className="bg-danger text-white">Score</th>
                                    <th className="bg-danger text-white">Score</th>
                                </tr>
                            </thead>
                            <tbody>

                                {getScored && getScored?.map((x, i) => {
                                    return (<tr key={i} >
                                        <td className='text-capitalize'>
                                            {x.c_name}
                                        </td>
                                        <td>
                                            {x.c_score}
                                        </td>
                                        <td>
                                            {x.c_score >= 16 ? <img src={winner1} width='50px' /> : 12 <= x.c_score >= 15 ? <img src={winner2} width='45px' /> : <img src={winner3} width='35px' />}
                                        </td>

                                    </tr>)

                                })}
                            </tbody>
                        </table>

                    </div>

                </div>
            </div>
        </div>
    );
}

export default OwnerChallengeScoreCard;