import React, { useState, useEffect, useContext } from "react";
import { setLocalStorage, stringToBoolean, loadImage } from '../../utilities/setLocalstorage';
import { DareClientContext } from '../../App';
import OwnerCompeletionCard from './OwnerCompeletionCard';

import click from '../../Assets/click.wav';

function OwnerQuizContainer() {

    const { naviagation, handleUserClick } = useContext(DareClientContext);
    const [currentQuestion, setCurrentQuestion] = useState([]);
    const [selectionFlag, setSelectionFlag] = useState(false);
    const [currentQuestionIndexTab, setcurrentQuestionIndexTab] = useState(0);
    const [AnswersQuestion, setAnswersQuestion] = useState([]);
    const [isLoading, SetisLoading] = useState(true)

    const handleNextQuestion = (e) => {
        let ClickSelectCard = new Audio(click);
        ClickSelectCard.play();
        setTimeout(() => {
            setSelectionFlag(true);
        }, 200);
        if (currentQuestionIndexTab <= 20) {
            const splitanservalue = e.split(" ");
            currentQuestion[currentQuestion?.findIndex(x => x.q_id === +splitanservalue[1])].isAnswered = true;
            currentQuestion[currentQuestion?.findIndex(x => x.q_id === +splitanservalue[1])].owner_a_id = +splitanservalue[0];
            Promise.all(currentQuestion.filter(x => x.isAnswered !== true)[1]?.Answers?.map((choice, i) =>
                loadImage(`${process.env.REACT_APP_BASE_URL}/public/${choice.a_thumb}`)
            ))
                .then()
                .catch(err => console.log("Failed to load images", err))
            setTimeout(() => {
                setAnswersQuestion([...AnswersQuestion, currentQuestion?.filter(x => x.isAnswered === true)[0]].flat());
                setCurrentQuestion([...currentQuestion.filter(x => x.isAnswered !== true)])
                setcurrentQuestionIndexTab(AnswersQuestion.length + 1);
                setSelectionFlag(false);
            }, 1000);
        }
    };

    const handleSkipQuestion = () => {
        setCurrentQuestion([currentQuestion.slice(1, currentQuestion.length), currentQuestion.slice(0, 1)].flat());
    };

    const OwnerQuizaswerSubmitted = async (quiz_performer, quiz_data) => {
        const response = await fetch(`${process.env.REACT_APP_BASE_URL}/createQuiz`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json'
            },
            body: (JSON.stringify({
                "quiz_performer": quiz_performer,
                "quiz_data": quiz_data,
                "t_id": naviagation.quizType ? naviagation.quizType : 1
            }))
        })
        if (!response.ok) {
            throw new Error('Data coud not be fetched!')
        } else {
            return response.json()
        }
    }

    useEffect(() => {
        // console.log(naviagation)
        const fetchData = async () => {
            const response = await fetch(`${process.env.REACT_APP_BASE_URL}/getQuestionsWithAnswers/${naviagation.quizType ? naviagation.quizType : 1}`)
            if (!response.ok) {
                throw new Error('Data coud not be fetched!')
            } else { return response.json() }
        }
        if (currentQuestion.length <= 0) {
            fetchData()
                .then((res) => {
                    setCurrentQuestion(res)
                }).finally(() => {
                    SetisLoading(false)
                })
        }

        if (currentQuestionIndexTab === 20) {
            OwnerQuizaswerSubmitted(stringToBoolean(naviagation.quizOwnerName) ? naviagation.quizOwnerName : 'Unkown', AnswersQuestion.map((item) => ({
                [item.q_id.toString()]: item.owner_a_id.toString(),
            }))).then((res) => {
                if (res.message === "Quiz created successfully") {
                    setLocalStorage({ ...naviagation, "isOwnerQuestionCompleted": true, "OwnerQuizs": [...naviagation?.OwnerQuizs, { "OwnerQuizId": res.newQuizData.quiz_uid, "quizType": naviagation.quizType ? naviagation.quizType : 1, "CreatedAt": new Date() }] })
                    handleUserClick({ "isOwnerQuestionCompleted": true, "OwnerQuizs": [...naviagation?.OwnerQuizs, { "OwnerQuizId": res.newQuizData.quiz_uid, "quizType": naviagation.quizType ? naviagation.quizType : 1, "CreatedAt": new Date() }] })
                }
            }).catch(
            )
        }
    }, [AnswersQuestion]);

    return (
        <div className="container">
            {!naviagation?.OwnerQuizs?.some(x => x.quizType === naviagation?.quizType) &&
                <div className="col-md-8 col-sm-12 offset-md-2 mt-4">
                    <div className="card-header card mt-3 px-3 shadow-sm">

                        <div className="countdown">
                            <ul className="p-0">
                                <li className={currentQuestionIndexTab === 0 ? "active" : ""}>1</li>
                                <li className={currentQuestionIndexTab === 1 ? "active" : ""}>2</li>
                                <li className={currentQuestionIndexTab === 2 ? "active" : ""}>3</li>
                                <li className={currentQuestionIndexTab === 3 ? "active" : ""}>4</li>
                                <li className={currentQuestionIndexTab === 4 ? "active" : ""}>5</li>
                                <li className={currentQuestionIndexTab === 5 ? "active" : ""}>6</li>
                                <li className={currentQuestionIndexTab === 6 ? "active" : ""}>7</li>
                                <li className={currentQuestionIndexTab === 7 ? "active" : ""}>8</li>
                                <li className={currentQuestionIndexTab === 8 ? "active" : ""}>9</li>
                                <li className={currentQuestionIndexTab === 9 ? "active" : ""}>10</li>
                                <li className={currentQuestionIndexTab === 10 ? "active" : ""}>11</li>
                                <li className={currentQuestionIndexTab === 11 ? "active" : ""}>12</li>
                                <li className={currentQuestionIndexTab === 12 ? "active" : ""}>13</li>
                                <li className={currentQuestionIndexTab === 13 ? "active" : ""}>14</li>
                                <li className={currentQuestionIndexTab === 14 ? "active" : ""}>15</li>
                                <li className={currentQuestionIndexTab === 15 ? "active" : ""}>16</li>
                                <li className={currentQuestionIndexTab === 16 ? "active" : ""}>17</li>
                                <li className={currentQuestionIndexTab === 17 ? "active" : ""}>18</li>
                                <li className={currentQuestionIndexTab === 18 ? "active" : ""}>19</li>
                                <li className={currentQuestionIndexTab === 19 ? "active" : ""}>20</li>
                            </ul>
                        </div>

                        <div className="mb-3 text-center">
                            <button className="bg-danger btn text-white p-2" value="0" onClick={!selectionFlag ? handleSkipQuestion : ''}>Skip This Question</button>
                        </div>

                        {!isLoading && currentQuestion && <div className="text-center">
                            <h4>{currentQuestion[0]?.q_title}</h4>
                            <div className="row">

                                {currentQuestion[0]?.Answers?.map((choice, i) => (
                                    <div className={selectionFlag ? 'col-6 col-sm-6 mb-3 cursor-grabbing mt-5' : 'col-6 col-sm-6 mb-3 cursor-grab mt-5'} key={i} onClick={!selectionFlag ? () => handleNextQuestion(choice.a_id + " " + choice.q_id) : null}>
                                        <div className={currentQuestion[0]?.owner_a_id === +choice?.a_id ? 'bg-info-subtle card border-radius-10 shadow' : currentQuestion[0]?.owner_a_id !== +choice?.a_id && selectionFlag ? 'bg-warning-subtle card border-radius-10 shadow' : 'card border-radius-10 shadow'} >
                                            <div className="card-body-img position-relative">
                                                <img src={`${process.env.REACT_APP_BASE_URL}/public/${choice.a_thumb}`} alt={choice.a_text} />
                                            </div>
                                            <div>
                                                <p className="fw-bolder p-0 w-100" >
                                                    {choice.a_text}
                                                </p>
                                            </div>
                                        </div>
                                    </div>
                                ))}
                            </div>
                        </div>}
                        {isLoading &&
                            <div className="text-center my-4">
                                <div className="spinner-border text-primary" role="status">

                                </div>
                                <div>
                                    Is Loading...
                                </div>
                            </div>
                        }
                    </div>
                </div>
            }
            {naviagation?.OwnerQuizs?.some(x => x.quizType === naviagation?.quizType) && <OwnerCompeletionCard />}
        </div>
    );
}

export default OwnerQuizContainer;
