import { useContext, useEffect, useState } from 'react';
import { DareAdminContext } from '..//AdminLayout'
import Modal from 'react-bootstrap/Modal';
import Button from 'react-bootstrap/Button';

function CategoryList() {
    const { handleAdminClick, token } = useContext(DareAdminContext);

    // const fetchData = async (username, password) => {
    //     const response = await fetch(`${process.env.REACT_APP_BASE_URL}/login`, {
    //         method: 'POST',
    //         headers: {
    //             'Content-Type': 'application/json'
    //         },
    //         body: (JSON.stringify({
    //             "username": username,
    //             "password": password
    //         }))
    //     })
    //     if (!response.ok) {
    //         throw new Error('Data coud not be fetched!')
    //     } else {
    //         return response.json()
    //     }
    // }
    // const onSumitSignForm = (e) => {
    //     e.preventDefault();
    //     if (e.target.elements.username.value && e.target.elements.password.value) {
    //         fetchData(e.target.elements.username.value, e.target.elements.password.value).then((data) => {
    //             if (data.message === "Login Successfull.") {
    //                 handleAdminClick({ "SignIn": JSON.stringify(data) })
    //             }
    //         }).catch();
    //     }
    // }

    const ViewQuestion = (e) => {
        // fetchQuiz(e.q_id).then((e) => {
        //     setShowListAnswer(e);
        handleShowView()
        // }).catch((ea) => {console.log(ea)})
    }


    // const [ShowEditText, setShowEditText] = useState();
    const [ShowEditList, setShowEditList] = useState({});

    const handleCloseEdit = (e) => setShowEditList({ isOpen: false });
    // const handleShowEdit = (e) => setShowEditList({isOpen:true});
    // const [editCategoryImg, seteditCategoryImg] = useState();
    // const [editcategoryName, seteditcategoryName] = useState();
    const [editcategoryName, seteditcategoryName] = useState();

    const EditCategoryModal = (e) => {
        // console.log(e)
        // console.log({ isOpen: true, ...e })
        setShowEditList({ isOpen: true, ...e })
        // seteditcategoryName(e.t_title)
        // seteditCategoryImg(`${process.env.REACT_APP_BASE_URL}/public${e?.t_image}`)

        // console.log(ShowEditList)
        // handleShowEdit({})
    }

    // const handleEditImageChange = (e) => {
    //     const file = e.target.files[0];
    //     if (file) {
    //         // seteditCategoryImg(file);
    //     }
    // };
    const toDataURL = url => fetch(url)
        .then(response => response.blob())
        .then(blob => new Promise((resolve, reject) => {
            const reader = new FileReader()
            reader.onloadend = () => resolve(reader.result)
            reader.onerror = reject
            reader.readAsDataURL(blob)
            // console.log(blob)
        }))

    function dataURLtoFile(dataurl, filename) {
        var arr = dataurl.split(','), mime = arr[0].match(/:(.*?);/)[1],
            bstr = atob(arr[1]), n = bstr.length, u8arr = new Uint8Array(n);
        while (n--) {
            u8arr[n] = bstr.charCodeAt(n);
        }
        return new File([u8arr], filename, { type: mime });
    }

    const handleSubmitEditCategory = async (e) => {
        const formData = new FormData();
        formData.append('t_title', ShowEditList?.t_title);
        if (typeof ShowEditList?.t_image == "string" && ShowEditList.t_image) {
            // ${process.env.REACT_APP_BASE_URL}/public${ShowEditList?.t_image}
            toDataURL(ShowEditList.t_image)
                .then(dataUrl => {
                    // console.log('Here is Base64 Url', dataUrl)
                    var fileData = dataURLtoFile(dataUrl, "imageName.jpg");
                    // console.log("Here is JavaScript File Object", fileData)
                    formData.append('image', fileData);
                })
        } else {
            formData.append('image', ShowEditList.t_image);
        }

        console.log(formData)
        try {
            await fetch(
                `${process.env.REACT_APP_BASE_URL}/updateType/${ShowEditList.t_id}`,
                {
                    method: "POST",
                    headers: {
                        Authorization: token.accessToken,
                    },
                    body: formData,
                }
            ).then((e) => {
                handleCloseEdit()
                console.log(e)
            });
        } catch (error) {
            console.error("Error:", error);
        }
    };



    const filterTable = (e) => {
        const searchText = e.target.value.toLowerCase();
        let searchData = categorylist.filter(emp => emp.t_title.toLowerCase().includes(searchText));
        setFilterData(searchData)
    }

    const [filteredData, setFilterData] = useState();
    const [quiz, SetQuiz] = useState([]);
    const [ShowViewAnswer, setShowViewAnswer] = useState(false);
    const handleCloseView = () => setShowViewAnswer(false);
    const handleShowView = () => setShowViewAnswer(true);
    const [ShowListAnswer, setShowListAnswer] = useState([]);

    const [ShownewCategory, setShownewCategory] = useState(false);
    const handleOpemNewCategory = () => { setnewCategoryImg(); setShownewCategory(true) };
    const handleCloseNewCategory = () => setShownewCategory(false);
    const [newCategoryImg, setnewCategoryImg] = useState();
    const [categoryName, setcategoryName] = useState();



    const handleCategoryImageChange = (e) => {
        const file = e.target.files[0];
        if (file) {
            setnewCategoryImg(file);
        }
    };

    const handleSubmitCategory = async (e) => {
        e.preventDefault();
        const formData = new FormData();
        // console.log(categoryName)
        // console.log(newCategoryImg)
        formData.append('t_title', categoryName);
        formData.append('image', newCategoryImg);

        try {
            await fetch(
                `${process.env.REACT_APP_BASE_URL}/createType`,
                {
                    method: "POST",
                    headers: {
                        Authorization: token.accessToken,
                    },
                    body: formData,
                }
            ).then((e) => {
                handleCloseNewCategory()
                fetchDataList()
                // handleAdminClick({ "updateAnswerById": true })
                // console.log(e)
            });
        } catch (error) {
            console.error("Error:", error);
        }
    };

    const [categorylist, setCategoryList] = useState();

    const fetchDataList = () => {
        const fetchData = async () => {
            const response = await fetch(`${process.env.REACT_APP_BASE_URL}/getAllTypes`,
                { method: "GET", headers: { Authorization: token.accessToken } });
            if (!response.ok) {
                throw new Error('Data coud not be fetched!')
            } else {
                return response.json()
            }
        }

        fetchData().then((res) => {
            setCategoryList(res);
            setFilterData(res);
            // console.log(res)
        }).catch(
            er => {
                console.log(er)
            }
        )
    }

    useEffect(() => {
        // console.log(token)
        fetchDataList()
    }, [])



    const [Showdelete, setShowDelete] = useState({ isOpen: false });

    const DeleteQuestion = (e) => {
        // console.log({ isOpen: true, ...e })
        setShowDelete({ isOpen: true, ...e })
        // console.log(e?.isDelete)
        if (e?.isDelete) {
            try {
                fetch(`${process.env.REACT_APP_BASE_URL}/deleteType/${e.t_id}`,
                    { method: "DELETE", headers: { Authorization: token.accessToken } }
                ).then((res) => {
                    // console.log(res)
                    if (res?.statusText === "OK") {
                        fetchDataList()
                        //  handleAdminClick({ "deleteQuestionById": true })
                    }
                });
            } catch (error) {
                fetchDataList()
                console.error("Error:", error);
            }
        }


    }

    // const handleResponce = (e) => {
    //     console.log(e)
    //     if (e) {
    //         setShowEditAnswer(false)
    //         DelQuizItem().then(e => {
    //             handleRootClick({ "toast": `Quiz ID ${naviagation?.OwnerQuizId} deleted successfully` })
    //             CreateOwnNewChallenge(naviagation?.OwnerQuizId)
    //             setTimeout(() => {
    //                 navigate('/en/');
    //             }, 500);
    //         }
    //         ).catch((eas) => {
    //             CreateOwnNewChallenge(naviagation?.OwnerQuizId)
    //             setTimeout(() => {
    //                 navigate('/en/');
    //             }, 500);
    //         })
    //     }
    //     else {
    //         // setTimeout(() => {
    //         navigate('/en/');
    //         // }, 500);
    //     }

    // }

    return (
        <div className="container my-3">
            <div>
                <Modal
                    show={Showdelete?.isOpen}
                    onHide={() => setShowDelete({ isOpen: false })}
                    backdrop="static"
                    keyboard={false}
                >
                    <Modal.Header closeButton>
                        <Modal.Title><h6>Confirmation </h6> </Modal.Title>
                    </Modal.Header>
                    <Modal.Body>
                        <p>Are you sure you want to inactive this questions</p>
                    </Modal.Body>
                    <Modal.Footer>
                        <Button variant="secondary" onClick={() => setShowDelete({ isOpen: false })}>
                            No
                        </Button>
                        <Button variant="primary" onClick={() => DeleteQuestion({ isDelete: true, ...Showdelete })}>
                            Yes
                        </Button>
                    </Modal.Footer>
                </Modal>

            </div>



            <Modal
                show={ShowEditList.isOpen}
                onHide={handleCloseEdit}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title><p> Category Edit Name</p> </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div >
                        <div className="d-flex justify-content-between">
                            <div className="container">
                                <label htmlFor="Answer">Enter Category Name</label>
                                <input className="form-control"
                                    name="a_text"
                                    placeholder="Enter Answer"
                                    value={ShowEditList.t_title}
                                    onChange={(e) => setShowEditList({ ...ShowEditList, t_title: e.target.value })}
                                />

                                <div className="form-group mt-3">
                                    <label htmlFor="picturePath">Select Image For answer</label>
                                    <input type="file" name="img" className="form-control" onChange={(e) => { console.log({ ...ShowEditList, t_image: e.target.files[0] }); setShowEditList({ ...ShowEditList, t_image: e.target.files[0] }) }} placeholder="select image" required="{true}" />
                                    <span className="text-danger"> Image must be 200X200 pixel in size</span>
                                    <div>
                                        <img className='w-100' src={typeof ShowEditList.t_image == "string" ? `${process.env.REACT_APP_BASE_URL}/public/${ShowEditList?.t_image}` : ShowEditList.t_image?.type == "image/png" ? URL.createObjectURL(ShowEditList.t_image) : ''} />
                                    </div>
                                </div>
                            </div>
                        </div>

                    </div>

                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleCloseEdit}>
                        Close
                    </Button>
                    <Button variant="primary" onClick={(e) => handleSubmitEditCategory(e)}>
                        Save
                    </Button>
                </Modal.Footer>
            </Modal>

            <Modal
                show={ShownewCategory}
                onHide={handleCloseNewCategory}
                backdrop="static"
                keyboard={false}
            >
                <Modal.Header closeButton>
                    <Modal.Title><p> Add New Category</p> </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <div >
                        <div className='d-flex justify-content-between'>
                            {/* <img className='answerlist-img-edit col-4 mb-3' src={`${process.env.REACT_APP_BASE_URL}/public/${x.a_thumb}`} /> */}
                        </div>
                        <div className="d-flex justify-content-between" >
                            <div className="container">
                                <label htmlFor="Answer">Enter Category Name</label>
                                <input className="form-control"
                                    name="a_text"
                                    placeholder="Enter Answer"

                                    onChange={(e) => setcategoryName(e.target.value)}
                                />
                                <div className="form-group mt-3">

                                    <label htmlFor="picturePath">Select Image For answer</label>
                                    <input type="file" name="img" className="form-control" onChange={(e) => handleCategoryImageChange(e)} placeholder="select image" required="{true}" />
                                    <span className="text-danger"> Image must be 200X200 pixel in size</span>
                                </div>
                                <div>
                                    <img className='w-100' src={newCategoryImg ? URL.createObjectURL(newCategoryImg) : ''} />
                                </div>
                            </div>
                        </div>

                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <Button variant="secondary" onClick={handleCloseNewCategory}>
                        Close
                    </Button>
                    <Button variant="primary" onClick={handleSubmitCategory}>
                        Save
                    </Button>
                </Modal.Footer>
            </Modal>

            <div className="col-sm-12">

                <div className='text-end'>
                    <button onClick={handleOpemNewCategory} className=' btn bg-secondary text-white text-capitalize me-2'> Add new Category</button>
                </div>

                <div className="align-items-center d-flex justify-content-between mt-5">
                    <h4>Category List</h4>
                    <div className="dataTables_filter">
                        <label>Search:<input type="search" className="form-control form-control-sm" placeholder="" onChange={filterTable} /></label>
                    </div>
                </div>
                <hr />
                <table className="table table-striped">
                    <thead>
                        <tr>
                            <th className='text-center' scope="col">#</th>
                            <th className='text-center' scope="col">Category Title</th>
                            <th className='text-center' scope="col">No of Question</th>
                            <th className='text-center' scope="col">Publish</th>
                            <th className='text-center' scope="col">Image</th>
                            <th className='text-center' scope="col">Option</th>
                        </tr>
                    </thead>
                    <tbody>
                        {filteredData?.length > 0 && filteredData.map((e, i) => (
                            <tr key={i}>
                                <th className='text-center'>{e.t_id}</th>
                                <td className='text-center'> {e.t_title}</td>
                                <td className='text-center'>{e.t_status}</td>
                                <td className='text-center'> {e.t_status ? 'Yes' : 'No'}</td>
                                <td className='text-center'> <img className='max-width-100' src={`${process.env.REACT_APP_BASE_URL}/public/${e?.t_image}`} /></td>
                                <td className='text-center'>
                                    {/* <button className='btn btn-success me-2' onClick={() => { ViewQuestion(e) }} value={e.c_title} > View</button> */}
                                    <button onClick={() => { EditCategoryModal(e) }} value={e.c_id} className=' btn bg-secondary text-white text-capitalize me-2'> Edit</button>
                                    <button onClick={() => { DeleteQuestion(e) }} value={e.c_id} className=' btn btn-danger text-capitalize'> Delete</button></td>
                            </tr>))
                        }
                    </tbody>
                </table>
                <div>
                    <Modal
                        show={ShowViewAnswer}
                        onHide={handleCloseView}
                        backdrop="static"
                        keyboard={false}
                    >
                        <Modal.Header closeButton>
                            <Modal.Title><p> {ShowListAnswer?.q_title}</p> </Modal.Title>
                        </Modal.Header>
                        <Modal.Body>
                            <div className='row'>
                                {ShowListAnswer && ShowListAnswer?.Answers?.map((e, i) => {
                                    return <>
                                        <p className='col-8'>{e?.a_text}</p>
                                        <img className='answerlist-img col-4 mb-3' alt={e.a_thumb} src={`${process.env.REACT_APP_BASE_URL}/public/${e.a_thumb}`} />
                                    </>
                                })
                                }
                            </div>
                        </Modal.Body>
                        <Modal.Footer>
                            <Button variant="secondary" onClick={handleCloseView}>
                                Close
                            </Button>
                        </Modal.Footer>
                    </Modal>
                </div>
            </div>
        </div>
    );
}

export default CategoryList;




